body ul.tlt-menu {
  --transition-default: all .3s ease-in-out;
  --arrow-color: #fcfcfc;
  --drawer-background: #fcfcfc;
  --text-color: #000;
  --button-bg-default: #0f6292;
  --button-bg-hocus: #0d77b5;
}

.tlt-arrow .show-hide {
  height: 3rem;
}

.tlt-arrow .show-hide svg {
  height: 1rem;
  width: 1rem;
  transition: transform .3s ease-in-out;
  transform: rotate(-90deg);
}

.tlt-arrow .show-hide svg path {
  fill: var(--arrow-color);
}

.tlt-menu {
  background: var(--drawer-background);
  width: 10rem;
  z-index: 200;
  padding: .5rem;
  transition-property: left;
  transition-duration: 1s, .5s;
  transition-timing-function: cubic-bezier(.23, 1, .32, 1);
  position: fixed;
  top: 33%;
  left: -10rem;
  box-shadow: 1px 2px 3px #00000026;
  margin: 0 !important;
}

.tlt-menu ul.tabs {
  margin: 0;
}

.tlt-menu li a {
  color: var(--text-color);
  width: 100%;
  margin: .25rem .5rem;
  text-decoration: none;
  transition: text-decoration .4s linear;
  display: inline-block;
}

.tlt-menu li a:focus, .tlt-menu li a:hover {
  text-decoration: underline;
}

.tlt-menu.active {
  left: 0;
}

.tlt-menu.active .show-hide svg {
  transform: rotate(90deg);
}

.show-hide {
  background-color: var(--button-bg-default);
  cursor: pointer;
  transition: color var(--transition-default);
  border: 1px solid #fff;
  border-top-right-radius: 4px;
  border-bottom-right-radius: 4px;
  align-items: center;
  padding: 0 8px 0 9px;
  display: flex;
  position: absolute;
  top: 0;
  bottom: 0;
  right: -2.1rem;
}

.show-hide:focus, .show-hide:hover {
  background-color: var(--button-bg-hocus);
}

.show-hide:focus.active, .show-hide:hover.active {
  left: 0;
}

